<script>
import { Line } from 'vue-chartjs'
import { ChartDataLabels } from 'chartjs-plugin-datalabels';
export default {
  extends: Line, ChartDataLabels,
  props: [
    "loading", 
    "dataSets",
    "dataHeads",
    "label",
    "backgroundColor",
  ],

  data() {
    return {
      chartdata: {
        labels: this.dataHeads,
        datasets: [{
          label: this.label,
          borderColor: this.backgroundColor,
          data: this.dataSets,
          datalabels: {
						align: 'end',
						anchor: 'end'
          },
          pointRadius: 5,
          pointBackgroundColor: this.backgroundColor,
        }]
      },
      options: {
        elements: {
          line: {
            tension: 0
          }
        },
        layout: {
          padding: {
              left: 10,
              right: 10,
              top: 0,
              bottom: 0
          }
        },
        plugins: {
					datalabels: {
            borderRadius: 4,
            backgroundColor: 'white',
						color: 'black',
						font: {
							weight: 'bold'
						},
					}
        },
        scales: {
					yAxes: [{
						stacked: true
					}]
				},
        responsive: true,
        maintainAspectRatio: false
      }
    }
  },
  mounted () {
    if(!this.loading && this.loading != null) {
      this.renderChart(this.chartdata, this.options)
    }
  }
}
</script>
