<script>
import { Bar } from 'vue-chartjs'
import { ChartDataLabels } from 'chartjs-plugin-datalabels';

export default {
  extends: Bar, ChartDataLabels,
  props: [
    "loading", 
    "dataSets",
    "dataHeads",
    "label",
    "backgroundColor",
  ],

  data() {
    return {
      chartdata: {
        labels: this.dataHeads,
        datasets: [{
          label: this.label,
          backgroundColor: this.backgroundColor,
          data: this.dataSets,

        }]
      },
      options: {
        plugins: {
					datalabels: {
            align: 'end',
            anchor: 'end',
            borderRadius: 4,
            borderColor: 'black',
            backgroundColor: 'white',
						color: this.backgroundColor,
						font: {
							weight: 'bold'
						},
					}
        },
        scales: {
					yAxes: [{
						stacked: true
					}]
        },
        layout: {
          padding: {
              left: 10,
              right: 10,
              top: 0,
              bottom: 0
          }
        },
        responsive: true,
        maintainAspectRatio: false
      }
    }
  },
  mounted () {
    if(!this.loading && this.loading != null) {
      this.renderChart(this.chartdata, this.options)
    }
  }
}
</script>
