<template>
  <div class="analytics text-center">
    <div class="display-1 text-center mt-5">
      UERM E-Triage Dashboard
      <b>Monthly User Reports</b>
    </div>
    <v-container>
      <v-row align="center" justify="center">
        <v-col cols="12" sm="6" md="4">
          <v-menu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            transition="scale-transition"
            offset-y
            min-width="290px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="month"
                label="Month"
                prepend-icon="fas fa-calendar"
                readonly
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker v-model="month" type="month" no-title scrollable>
              <v-spacer></v-spacer>
              <v-spacer></v-spacer>
              <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
              <v-btn text color="primary" @click="changeDate">OK</v-btn>
            </v-date-picker>
          </v-menu>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-btn large outlined color="primary" @click="changeDate">SUBMIT</v-btn>
        </v-col>
      </v-row>
    </v-container>
    <v-container>
      <div class="text-center ma-12" v-if="this.loading">
        <v-progress-circular
          :size="150"
          class="mx-3"
          indeterminate
          color="primary"
        ></v-progress-circular>
      </div>
      <div v-if="!this.loading && this.loading != null">
        <v-row>
          <v-col cols="12">
            <v-card class="mx-auto d-flex align-center justify-center pa-3" light style="overflow-x: auto">
              <v-list-item three-line>
                <v-list-item-content>
                  <h3>Employee Census for {{this.monthSelected}}</h3>
                  <line-chart
                    v-if="!this.loading"
                    :dataHeads="this.getEmployeeDataSet.employeeDataSet.header"
                    :dataSets="this.getEmployeeDataSet.employeeDataSet.data"
                    :label="this.getEmployeeDataSet.employeeDataSet.label"
                    :backgroundColor="this.getEmployeeDataSet.employeeDataSet.backgroundColor"
                    :loading="this.loading"
                  />
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <v-card class="mx-auto d-flex align-center justify-center pa-3" light style="overflow-x: auto">
              <v-list-item three-line>
                <v-list-item-content>
                  <h3>Visitor Census for {{this.monthSelected}}</h3>
                  <bar-chart
                    v-if="!this.loading"
                    :dataHeads="this.getVisitorDataSet.visitorDataSet.header"
                    :dataSets="this.getVisitorDataSet.visitorDataSet.data"
                    :label="this.getVisitorDataSet.visitorDataSet.label"
                    :backgroundColor="this.getVisitorDataSet.visitorDataSet.backgroundColor"
                    :loading="this.loading"
                  />
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </v-col>
          <v-col>
            <v-card class="mx-auto d-flex align-center justify-center pa-3" light style="overflow-x: auto">
              <v-list-item three-line>
                <v-list-item-content>
                  <h3>TPC Census for {{this.monthSelected}}</h3>
                  <bar-chart
                    v-if="!this.loading"
                    :dataHeads="this.getTPCDataSet.tpcDataSet.header"
                    :dataSets="this.getTPCDataSet.tpcDataSet.data"
                    :label="this.getTPCDataSet.tpcDataSet.label"
                    :backgroundColor="this.getTPCDataSet.tpcDataSet.backgroundColor"
                    :loading="this.loading"
                  />
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <div class="display-1 text-center mt-5">
              ETRIAGE
              <b>COVID ER REPORT</b>
              FOR <span class="text-uppercase"> {{this.monthSelected}} </span>
            </div>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <v-card class="mx-auto d-flex align-center justify-center pa-3" light style="overflow-x: auto">
              <v-list-item three-line>
                <v-list-item-content>
                  <h3>ER Employee Census for {{this.monthSelected}}</h3>
                  <bar-chart
                    v-if="!this.loading"
                    :dataHeads="this.getEmployeeDataSet.employeeERDataSet.header"
                    :dataSets="this.getEmployeeDataSet.employeeERDataSet.data"
                    :label="this.getEmployeeDataSet.employeeERDataSet.label"
                    :backgroundColor="this.getEmployeeDataSet.employeeERDataSet.backgroundColor"
                    :loading="this.loading"
                  />
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <v-card class="mx-auto d-flex align-center justify-center pa-3" light style="overflow-x: auto">
              <v-list-item three-line>
                <v-list-item-content>
                  <h3>ER Visitor Census for {{this.monthSelected}}</h3>
                  <bar-chart
                    v-if="!this.loading"
                    :dataHeads="this.getVisitorDataSet.visitorERDataSet.header"
                    :dataSets="this.getVisitorDataSet.visitorERDataSet.data"
                    :label="this.getVisitorDataSet.visitorERDataSet.label"
                    :backgroundColor="this.getVisitorDataSet.visitorERDataSet.backgroundColor"
                    :loading="this.loading"
                  />
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </v-col>
          <v-col>
            <v-card class="mx-auto d-flex align-center justify-center pa-3" light style="overflow-x: auto">
              <v-list-item three-line>
                <v-list-item-content>
                  <h3>ER TPC Census for {{this.monthSelected}}</h3>
                  <bar-chart
                    v-if="!this.loading"
                    :dataHeads="this.getTPCDataSet.tpcERDataSet.header"
                    :dataSets="this.getTPCDataSet.tpcERDataSet.data"
                    :label="this.getTPCDataSet.tpcERDataSet.label"
                    :backgroundColor="this.getTPCDataSet.tpcERDataSet.backgroundColor"
                    :loading="this.loading"
                  />
                </v-list-item-content>
              </v-list-item>
            </v-card>
          </v-col>
        </v-row>
      </div>
    </v-container>
  </div>
</template>

<script>
// @ is an alias to /src
import LineChart from '@/components/charts/LineChart.vue'
import BarChart from '@/components/charts/BarChart.vue'
import { mapGetters } from 'vuex'

export default {
  name: 'MonthlyReport',
  components: {
    BarChart,
    LineChart,
  },
  computed: {
    ...mapGetters([
      "getEmployeeDataSet",
      "getVisitorDataSet",
      "getTPCDataSet"
    ]),
  },
  data() {
    return {
      month: new Date().toISOString().substr(0, 7),
      monthSelected: null,
      menu: false,
      loading: null,
    }
  },
  mounted() {
    
    if (!localStorage.isLoggedIn) {
      this.$router.push("/");
    }
  },
  methods: {
    async changeDate(){
      this.$refs.menu.save(this.month)
      this.getETriageAnalytics();
      const month = new Date(this.month)
      const monthNames = ["January", "February", "March", "April", "May", "June",
        "July", "August", "September", "October", "November", "December"
      ];
      const monthNow = monthNames[month.getMonth()];
      const yearNow = new Intl.DateTimeFormat('en', { year: 'numeric' }).format(month);
      this.monthSelected =  monthNow +' '+yearNow;
    },
    async getETriageAnalytics() {
      this.loading = true
      await this.$store.dispatch("getETriageAnalytics",this.month);
      this.loading = false
    },
  }
}
</script>
